import React from 'react';

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Countdown from 'react-countdown';

import './App.css';

const Completionist = () => <h1>Сервер открыт!</h1>;

function createLabel(number, titles) {
    const cases = [2, 0, 1, 1, 1, 2];
    return `${titles[number % 100 > 4 && number % 100 < 20 ? 2 : cases[number % 10 < 5 ? number % 10 : 5]]}`;
}

const renderer = ({ days, hours, minutes, seconds, completed }) => {
    if (completed) {
      // Render a completed state
      return <Completionist />;
    } else {
      // Render a countdown
      var prefix = createLabel(days, ['день', 'дня', 'дней']) ;
      return <h2>{days} {prefix} и {hours}:{minutes}:{seconds}</h2>;
    }
};

const App = () => (
    <>
        <Navbar bg="wight" expand="lg">
            <Container>
                <Navbar.Brand href="/">
                    <img src="/logo.png" width="32" height="32" className="d-inline-block align-top header-img" alt="" />{' '}
                    <span className='Navbar-title'>LazyLand</span>
                </Navbar.Brand>
                <Navbar.Collapse id="basic-navbar-nav" className='d-flex justify-content-end'>
                    <Nav className="me-auto justify-content-end d-flex">
                        <Nav.Link href="https://discord.gg/xrGna3M6s9">Дискорд</Nav.Link>
                        <Nav.Link href="https://docs.google.com/document/d/1-iOmTCcSNUyfM19MmTHZ7KQzRhz-vY4O-wQqXIw7M9I/edit">Правила</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
        <Container className="Header">
            <header className="App-header">
                <div className="right-side-phone">
                    <img src="/man.png" className="App-logo" alt="logo" />
                </div>
                <div className="left-side">
                    <h1 className="App-title">LazyLand</h1>
                    <p>
                        Ламповый ванильный сервер,<br />
                        Строй город, выживай или проводи ивенты,<br />
                        А главное успей подать заявку, пока не поздно!
                    </p>
                    <a href="https://docs.google.com/forms/d/e/1FAIpQLScvYH8osK5IaB7Vilf76vcYU4K77hvNBY8rPih9lc-idxkvJg/viewform?usp=sharing">
                        <button className="button1 btn btn-primary border-3 border-dark">Подать заявку &gt;</button>
                    </a>
                </div>
                <div className="right-side">
                    <img src="/man.png" className="App-logo" alt="logo" />
                </div>
            </header>
        </Container>
        <div className="Banner">
            <Container className="Banner-title">
                <h4>Сервер откроется через...</h4>
                <Countdown
                    date={'2022-12-7 17:00:00'}
                    renderer={renderer}
                />
            </Container>
        </div>
    </>
);

export default App;
